.notFound {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 40vh;
    justify-content: center;
    text-align: center;
  }
  
  .notFound p {
    font-size: 1.5rem;
  }
  
  .notFound p a {
    text-decoration: none;
    color: black;
    font-style: italic;
    font-weight: bold;
  }
  